:root {
  --color-main: red;
  --color-button-num: #85929e;
  --color-button-operator: #34495e;
  --color-button-clear: #ec7063;
  --color-background: red;
  --color-calc-background: #f2f4f4;
  --color-outside-border: #7b241c;
}
/*test for git*/
.holder-background {
  position: relative;
  width: 300px;
  height: 420px;
  padding: 20px;
  margin: 100px auto;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  border: 4px solid var(--color-outside-border);
  background: brown;
}

.holder {
  background-color: brown;
  position: absolute;
  top: -4px;
  left: -4px;
  width: 340px;
  height: 460px;
  border-radius: 4px;
  transform-origin: left;
  border: 4px solid var(--color-outside-border);
  /*ANIMATION TIME*/
  transition: all 1.5s ease-in-out;
}
.holder .text-box {
  background-color: white;
  margin: 80px auto 0 auto;
  width: 200px;
  border-radius: 8px;
  opacity: 0.5;
  /*ANIMATION TIME*/
  transition: all 1.5s ease-in-out;
}
.holder p {
  color: brown;
  text-align: center;
  font-weight: bold;
  font-size: 30px;
  /*ANIMATION TIME*/
  transition: all 1.5s ease-in-out;
  padding: 15px;
  border: 1px solid;
}
/*DOOR ANIMATION*/
.holderOpen {
  transform: perspective(1200px) translateZ(0px) translateX(0px) translateY(0px)
    rotateY(-110deg);
}
.holderOpen p {
  opacity: 0;
}
.holderOpen .text-box {
  background-color: brown;
}
.display-box {
  padding: 20px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background: var(--color-button-operator);
}

.sum-div,
.display-div {
  background: white;
  height: 30px;
  width: 260px;
}
.sum-div {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.display-div {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.sum-overflow,
.display-overflow {
  width: 240px;
  margin: 0 10px 0 10px;
  text-align: right;
  overflow: hidden;
  white-space: nowrap;
}
.sum {
  font-size: 20px;
  float: right;
  margin: 4px 4px 0 0;
}
#display {
  font-size: 26px;
  float: right;
  padding-left: 15px;
  margin: -1px 5px 0px 0;
}
.buttons {
  display: grid;
  grid-template-columns: repeat(4, 70px);
  grid-template-rows: repeat(5, 60px);
  margin: none;
  padding: 10px;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  background: var(--color-calc-background);
}
#clear {
  grid-area: 1/1/2/3;
}
#clearJustTheLastOne {
  grid-area: 1/3/2/5;
}
button {
  margin: 10px;
  color: white;
  border: none;
  border-radius: 4px;
  background: var(--color-button-num);
}
#clearJustTheLastOne,
#clear {
  background: var(--color-button-clear);
}
#divide,
#multiply,
#subtract,
#add,
#equals,
#decimal {
  background: var(--color-button-operator);
}

button:hover {
  opacity: 0.6;
}
